<h1>级联选择器 Cascader</h1>
<h3>基础用法</h3>
<p>由于选项默认可见，不宜过多，若选项过多，建议使用 Select 选择器。</p>
<demo-block>
                            <div class="source" slot="source">
  <as-row :offset="20">
    <as-col :col="3">
      <as-cascader :data="data" v-model="value"></as-cascader>
    </as-col>
    <as-col :col="3">
      <as-cascader :data="data" v-model="value2"></as-cascader>
    </as-col>
  </as-row>

  

</div>
