<h3>移入展开</h3>
<p>设置属性 trigger 为 hover，当鼠标悬停时就会展开子集。</p>
<demo-block>
                            <div class="source" slot="source">
  <as-row :offset="20">
    <as-col :col="3">
      <as-cascader :data="data" v-model="value2" clearable=""></as-cascader>
    </as-col>
    <as-col :col="3">
      <as-cascader trigger="hover" :data="data" v-model="value2"></as-cascader>
    </as-col>
  </as-row>

  

</div>
