<h3>自定义显示</h3>
<p>通过设置 slot 可以自定义显示内容，不局限于输入框。</p>
<demo-block>
                            <div class="source" slot="source">
  <as-row :offset="20">
    <as-col :col="3">{{text}}
      <as-cascader :data="data" @change="handleChange">
        <a href="javascript:void(0)">选择</a>
      </as-cascader>
    </as-col>
  </as-row>

  

</div>
