<h3>自定义已选项</h3>
<p>对于显示的结果，可以通过 render-format 接收一个函数来自定义。其中第一个参数 labels 是当前选择的label的集合，第二个参数 selectedData 是当前选择的数据集合，可以利用它们组合出你想要显示的内容。</p>
<demo-block>
                            <div class="source" slot="source">
  <as-row :offset="20">
    <as-col :col="3">{{text}}
      <as-cascader :data="data" :render-format="format"></as-cascader>
    </as-col>
  </as-row>

  

</div>
