<h2>属性说明</h2>
<h3>Cascader参数</h3>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>data</td>
<td>数据源</td>
<td>Array</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>searchable</td>
<td>是否支持筛选</td>
<td>boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>clearable</td>
<td>是否为支持清空选项</td>
<td>boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>disabled</td>
<td>是否可用</td>
<td>boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>trigger</td>
<td>触发的类型</td>
<td>string</td>
<td>hover/click</td>
<td>click</td>
</tr>
<tr>
<td>slot</td>
<td>自定义显示</td>
<td>-</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>render-format</td>
<td>自定义显示函数</td>
<td>Function</td>
<td>/</td>
<td>-</td>
</tr>
<tr>
<td>size</td>
<td>尺寸</td>
<td>string</td>
<td>lg/md(默认)/sm/xs</td>
<td>md</td>
</tr>
<tr>
<td>placeholder</td>
<td>没有内容的展示</td>
<td>string</td>
<td>-</td>
<td>Select Item</td>
</tr>
<tr>
<td>search-empty-text</td>
<td>没有数据时候的展示</td>
<td>string</td>
<td>-</td>
<td>there is no data</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
  data () {
    return {
      value: [],
      value2: ['jiangsu', 'suzhou', 'zhuozhengyuan'],
      data: [
        {
          value: 'beijing',
          label: '北京',
          children: [
            {
              value: 'gugong',
              label: '故宫'
            },
            {
              value: 'tiantan',
              label: '天坛'
            },
            {
              value: 'wangfujing',
              label: '王府井'
            }
          ]
        },
        {
          value: 'jiangsu',
          label: '江苏',
          children: [
            {
              value: 'nanjing',
              label: '南京',
              children: [
                {
                  value: 'fuzimiao',
                  label: '夫子庙',
                }
              ]
            },
            {
              value: 'suzhou',
              label: '苏州',
              children: [
                {
                  value: 'zhuozhengyuan',
                  label: '拙政园',
                },
                {
                  value: 'shizilin',
                  label: '狮子林',
                }
              ]
            }
          ]
        }
      ],
      data3: [
        {
          value: 'beijing',
          label: '北京',
          children: [
            {
              value: 'gugong',
              label: '故宫'
            },
            {
              value: 'tiantan',
              label: '天坛'
            },
            {
              value: 'wangfujing',
              label: '王府井'
            }
          ]
        },
        {
          value: 'jiangsu',
          label: '江苏',
          disabled: true,
          children: [
            {
              value: 'nanjing',
              label: '南京',
              children: [
                {
                  value: 'fuzimiao',
                  label: '夫子庙',
                }
              ]
            },
            {
              value: 'suzhou',
              label: '苏州',
              children: [
                {
                  value: 'zhuozhengyuan',
                  label: '拙政园',
                },
                {
                  value: 'shizilin',
                  label: '狮子林',
                }
              ]
            }
          ]
        }
      ],
      text: ''
    }
  },
  methods: {
    handleChange (value, selectedData) {
      this.text = selectedData.map(o => o.label).join(', ');
    },
    format (labels, selectedData) {
      const index = labels.length - 1;
      const data = selectedData[index] || false;
      if (data && data.code) {
        return labels[index] + ' - ' + data.code;
      }
      return labels[index];
    }
  }
}
</script>


